.blank
{
    width: 100%;
    height: 50px;
    background-color: #2A2A2A;
}
.brand
{
    display: flex;
    justify-content: space-evenly;
    padding-top: 50px;
    padding-bottom: 50px;
    text-align: start;
}
.brand h1
{
    font-family: 'Cinzel Decorative', cursive;
    color: #7B3F00;
}
.brand ul
{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.brand a
{
    text-decoration: none;
    color: #7B3F00;
}
.icon
{
    display: flex;
    gap: 20px;
}