.banner
{
    width: 100%;
    height: 600px;
    background-image: url("./banner3.jpg");
    background-size: cover;
    display: flex;
    flex-direction: column;
    gap: 60px;
    justify-content: center;
}
.banner h1
{
    color: white;
    font-family: 'Cinzel Decorative', cursive;
    font-weight: bold;
    font-size: 50px;
    word-spacing: 30px;
}
.banner p
{
    font-size: 20px;
    color: white;
}
.banner button
{
    width: 17%;
    background-color: #DFB300;
    padding: 13px;
    border: none;
    font-family: 'Ubuntu Mono', monospace;
    margin-left: 41.5%;
    font-size: 17px;
}
.banner button:hover
{
    background-color: #dec978;
}