.page h1
{
    font-family: 'Cinzel Decorative', cursive;
    color: #7B3F00;
    padding-top: 30px;
    padding-bottom: 30px;
}
.review
{
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: #7B3F00;
    padding-bottom: 50px;
}
.review a
{
    display: flex;
    flex-direction: column;
    align-items: end;

}
.review1
{
    width: 25%;
    height: 300px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.pic1
{
    display: flex;
    gap: 40px;
}
.text1
{
    text-align: start;
