.Nav
{
   width: 100%;
   height: 70px;
}
.logo
{
    display: flex;
    gap: 10px;
    text-align: center;
    align-items: center;
}
.logo h1
{
    font-size: 25px;
    text-align: center;
    font-family: 'Cinzel Decorative', cursive;
}
.logo h2
{
   color: #7B3F00;
   font-weight: bolder;
   font-size: 30px;
   font-family: 'Cinzel Decorative', cursive;
}
.Nav
{
    display: flex;
    background-color:#2A2A2A;
    justify-content: space-around;
    color: white;
}
.direction
{
    display: flex;
    align-items: center;
    gap: 30px;
}
.direction link
{
    text-decoration: none;
}
.search
{
    width: 200px;
    height: 25px;
    background-color: white;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px;
    border-radius: 25px;
}
.search input
{
    border: none;
    outline: none;
}